// vim: sts=2 sw=2 ts=2 expandtab

/*
 * 
 *
 * 
 */

// 
import "normalize.css/normalize.css";
import "balloon-css/balloon.css";
import "../css/mplus_webfonts.css";
import "../css/global.css";

// 
import React, { Component } from "react";
import ReactDOM from "react-dom";
import { createStore, combineReducers, applyMiddleware } from "redux";
import createLogger from "redux-logger";
import thunkMiddleware from "redux-thunk";
import { Provider } from "react-redux";
import counterpart from "counterpart";
import Translate from "react-translate-component";

// 
import Calculator from "./calculator.jsx";
import * as reducers from "./reducers.js";


// 
import locale_ja from "./locale/ja";
counterpart.registerTranslations("ja", locale_ja);

// 
counterpart.setLocale("ja");

// 
const middlewares = [thunkMiddleware];
if (process.env.NODE_ENV === "development") {
  // redux
  // state
  const loggerMiddleware = createLogger();
  middlewares.push(loggerMiddleware);
}

// reducerreducer
// state****
const reducer = combineReducers(
  {
    job: reducers.job_data,
    weapon: reducers.weapon,
    summon: reducers.summon,
    basicinfo: reducers.basicinfo,
    component_state: reducers.component_state,
    inputlock: reducers.inputlock_counter,
    friend: reducers.friend
  }
);
// reducerredux store
const store = createStore(
  reducer,
  applyMiddleware(...middlewares)
);


// 
// Providerreact-reduxconnect
class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Calculator />
      </Provider>
    );
  }
}


// DOM
ReactDOM.render(
   <App />,
  document.getElementById('react_content')
);



