// 
export function set_affinity(affinity) {
  return function (dispatch) {
    if (affinity == "good" || affinity == "bad") {
      dispatch({ type: RC.basic.AFFINITY, value: affinity });
    } else {
      dispatch({ type: RC.basic.AFFINITY, value: "normal" });
    }
  };
}

